import React from 'react';
import { graphql, Link } from 'gatsby';
import Layout from '../components/layout';
import '../components/bootstrap.css';
import {Row,Col} from "reactstrap";
import {ThemeToggler} from "gatsby-plugin-dark-mode";
import Toggle from "react-toggle";
import { withPrefix } from 'gatsby'
const BlogTemplate = ({ data }) => {
    const { markdownRemark } = data;
    const { frontmatter, html } = markdownRemark;

    function handleLink(e) {
        e.preventDefault();
        window.location.replace("/blogs");
    }
    return (
        <ThemeToggler>
            {({ theme, toggleTheme }) => (
        <Layout >
            <div className="blogHeader" style={{verticalAlign: "middle", height: "100px",width: "100vw", display: "table-cell"}}>

                    <span style={{fontSize: "0.8em"}} onClick={handleLink} >Aman Dave</span>
                    <label style={{float: "right"}}>
                        <Toggle
                            className='dark-theme'
                            onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
                            value={theme === 'dark'}
                            icons={{
                                checked: <img src={require('../images/sun.png')} height="20px" style={{marginTop :"-5px", marginLeft: "-5px"}} />,
                                unchecked:<img src={require('../images/moon.png')} height="20px" style={{marginTop :"-5px", marginLeft: "-5px"}}  />
                            }}/>
                    </label>
            </div>

            <section>
                <div className="blogTitle">
                    <h1>{frontmatter.title}
                        {(frontmatter.title==='Resume')
                            ?<span>
                            <a style={{float: "right", fontSize: "17px"}} href={withPrefix('resume.pdf')}  target="_blank" download="Aman_Dave_Resume">Download in PDF format</a>
                            </span>
                            :
                            null
                        }
                    </h1>
                    <span>{frontmatter.date}</span>
                </div>
                <br/>
                <hr/>
                <br/>

                {/*<embed src={withPrefix('resume.pdf')} width="800px" height="100%" />*/}

                <div className="blogText"
                     dangerouslySetInnerHTML={{ __html: html }} />
            </section>
        </Layout>
            )}
        </ThemeToggler>
    );
};

export default BlogTemplate;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
      }
    }
  }
`;
